@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.app{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #333;
}

.app-wrap{
  padding: 1rem;
  width: 375px;
  height: 700px;
  border-radius: 5px;
  box-shadow:
  0 1.3px 6.2px rgba(0, 0, 0, 0.2),
  0 4.2px 11.6px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  flex-direction: column;
}

header{
  margin-bottom: 80px;
  width: 100%;
  text-align: center;
}

.input{
  border: none;
  padding: 0.5rem;
  border-radius: 2px;
  border: 1px solid #ccc;
  margin-bottom: 20px;
  width: 70%;
  transition: 0.3s;
  background-color: transparent;
}

.input:focus{
  width: 90%;
}

.input:focus{
  outline: none;
}

.app .app-wrap input::-webkit-input-placeholder{
  color: #888;
}

.app .app-wrap input:-ms-input-placeholder{
  color: #888;
}

.app .app-wrap input::-ms-input-placeholder{
  color: #888;
}

.app .app-wrap input::placeholder{
  color: #888;
}

.date{
  font-size: 1.2rem;
  text-align: center;
}

.date span{
  animation: dateDot infinite 1s;
  vertical-align: text-bottom;
}

@keyframes dateDot{
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

.container{
  width: 100%;
  animation: fadeUp 1s forwards;
}

@keyframes fadeUp{
  0%{
    opacity: 0;
    transform: translateY(25px);
  }
  100%{
    opacity: 1;
    transform: translateY(0px);
  }
}

.content{
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 100px;
}

.content h1{
  font-size: 6rem;
}

.content h1 span{
  font-size: 2.5rem;
  vertical-align: text-top;
}

.content .icon{
  text-align: center;
}

.content .icon i{
  font-size: 4rem;
}

.content h4 {
  font-size: 1.8rem;
  text-transform: uppercase;
}

.content h6 {
  font-size: 0.9rem;
}

.sub-content{
  display: flex;
}

.sub-content div{
  width: calc(33.333%);
  font-size: 1.2rem;
  text-align: center;
}

.sub-content .fade01{
  animation: fadeUp01 1s forwards;
  animation-delay: 0.3s;
  opacity: 0;
}

@keyframes fadeUp01{
  0%{
    opacity: 0;
    transform: translateY(10px);
  }
  100%{
    opacity: 1;
    transform: translateY(0px);
  }
}

.sub-content .fade02{
  animation: fadeUp02 1s forwards;
  animation-delay: 0.5s;
  opacity: 0;
}

@keyframes fadeUp02{
  0%{
    opacity: 0;
    transform: translateY(10px);
  }
  100%{
    opacity: 1;
    transform: translateY(0px);
  }
}

.sub-content .fade03{
  animation: fadeUp03 1s forwards;
  animation-delay: 0.8s;
  opacity: 0;
}

@keyframes fadeUp03{
  0%{
    opacity: 0;
    transform: translateY(10px);
  }
  100%{
    opacity: 1;
    transform: translateY(0px);
  }
}

.sub-content div:not(:last-child){
  border-right: 1px solid #888;
}

.sub-content p.title{
  font-size: 0.9rem;
}

.sub-content p{
  font-size: 1.2rem;
}
.app.cloud{
  background: linear-gradient(to bottom, rgba(255,255,255,0.5) 0%, rgba(0,0,0,0.5) 100%)
}

.app.cloud .app-wrap{
  background: linear-gradient(to bottom, rgba(255,255,255,0.1) 0%, rgba(0,0,0,0.1) 100%), radial-gradient(at top center, rgba(255,255,255,0.20) 0%, rgba(0,0,0,0.20) 120%) #989898;
 background-blend-mode: multiply,multiply;
 color: #eee
}

.app.cloud .app-wrap input{
 color: #eee;
}

.app.cloud .app-wrap input::-webkit-input-placeholder{
  color: #ddd;
}

.app.cloud .app-wrap input:-ms-input-placeholder{
  color: #ddd;
}

.app.cloud .app-wrap input::-ms-input-placeholder{
  color: #ddd;
}

.app.cloud .app-wrap input::placeholder{
  color: #ddd;
}

.app.rain{
  background-image: linear-gradient(to top, #accbee 0%, #e7f0fd 100%);
}

.app.rain .app-wrap{
  background-image: linear-gradient(to top, #accbee 0%, #e7f0fd 100%);
}

.app.clear{
  background-image: linear-gradient(-225deg, rgba(158,251,211,0.8) 0%,rgba(87,233,242,0.8) 48%, rgba(145, 154, 156, 0.8) 100%);
}

.app.clear .app-wrap{
  background-image: linear-gradient(-225deg, #9EFBD3 0%, #57E9F2 48%, #45D4FB 100%);
}

.app.clear .app-wrap input{
  border: 1px solid #333;
}

.app.snow{
  background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}

.app.snow .app-wrap{
  background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
}

.app.mist{
  background-image: linear-gradient(to top, #ace0f9 0%, #fff1eb 100%);
}

.app.mist .app-wrap{
  background-image: linear-gradient(to top, #fff1eb 0%, #ace0f9 100%);
}
 
 .app.mist .app-wrap input{
  border: 1px solid #333;
}
 

